import { ImmortalDB } from 'immortal-db';

const POS_GA_SESSION_ID_STORE_KEY = 'GA_SESSION_ID';

//We must always pass some posGaSessionId to the backend, otherwise it will send the event missing
//cid field to the server, which will be ignored
export const generateFallbackPosGaSessionId = (): Promise<string> => {
  return ImmortalDB.get(POS_GA_SESSION_ID_STORE_KEY).then((sessionId) => {
    if (!sessionId) {
      //Imitating the session id format used by GA itself
      const newSessionId =
        Math.floor(Math.random() * 9999999999 + 1) + '.' + Math.floor(Math.random() * 9999999999 + 1);
      ImmortalDB.set(POS_GA_SESSION_ID_STORE_KEY, newSessionId);
      return newSessionId;
    }
    return sessionId;
  });
};
