import { ImmortalDB } from 'immortal-db';

const API_THROTTLING_PERIOD_IN_SECONDS = 60;
const INITIAL_CACHE_USAGE_PERIOD_IN_SECONDS = 15;

const getCurrentTimestamp = (): number => {
  return new Date().getTime();
};

const getSecondsSince = (baseTimestamp: number): number => {
  return (getCurrentTimestamp() - baseTimestamp) / 1000;
};

const CART_SIZE_STORE_KEY = 'tipserCartSize';
const INITIAL_CART_SIZE_ACCESS_TIME_INVALIDATED = -1;

const toNumber = (value: string) => parseInt(value, 10);

export class CartSizeCache {
  //a timestamp for last time request to API was made
  lastApiSyncTime: number;
  //a timestamp for initial cart size request, to determine when to invalidate the cookies
  initialCartSizeAccessTime: number;

  private getCachedCartSize(): Promise<number> {
    return ImmortalDB.get(CART_SIZE_STORE_KEY).then(toNumber);
  }

  private updateCachedCartSize(cartSize: number): void {
    ImmortalDB.set(CART_SIZE_STORE_KEY, `${cartSize}`);
  }

  public getCartSizeFromCache(): Promise<number> {
    //If this is the first access to the cache during the current page view
    if (!this.initialCartSizeAccessTime) {
      this.initialCartSizeAccessTime = getCurrentTimestamp();
      return this.getCachedCartSize();
    }

    //In case was invalidated we want first access time to go out of our way and do the API request
    if (this.initialCartSizeAccessTime === INITIAL_CART_SIZE_ACCESS_TIME_INVALIDATED) {
      this.initialCartSizeAccessTime = getCurrentTimestamp();
      return null;
    }

    //If access to cache was within initial "use cached value" period OR cache is still valid since last sync
    if (
      getSecondsSince(this.initialCartSizeAccessTime) < INITIAL_CACHE_USAGE_PERIOD_IN_SECONDS ||
      (this.lastApiSyncTime && getSecondsSince(this.lastApiSyncTime) < API_THROTTLING_PERIOD_IN_SECONDS)
    ) {
      return this.getCachedCartSize();
    }

    //If cache has never been synced yet or it expired since the last sync
    return null;
  }

  public invalidateCache() {
    this.lastApiSyncTime = null;
    this.initialCartSizeAccessTime = INITIAL_CART_SIZE_ACCESS_TIME_INVALIDATED;
  }

  public syncCartSizeFromApi(cartSize: number): void {
    this.lastApiSyncTime = getCurrentTimestamp();
    this.updateCachedCartSize(cartSize);
  }

  public incrementCachedCartSizeInCache(): void {
    this.getCachedCartSize().then((cachedCartSize) => {
      if (cachedCartSize != null) {
        this.updateCachedCartSize(cachedCartSize + 1);
      }
    });
  }
}
