import './polyfills';

import { initConfig, TipserSdkConfig, TipserSdkConfigCore } from './config';
import { isConfigValid, validateConfig } from './config-validation';
import { TipserApiClient, GaEventPayload } from './tipser-api-client';
import { loadStyles } from './load-styles';

const stylesAsString = require('./widget.less');

import './logger';

loadStyles(stylesAsString);

export * from './utils';
export { TipserSdkConfig, TipserEnv, TipserLang, ModalUiSettings } from './config';
export { GaEventPayload } from './tipser-api-client';

//hash handler and dialogsManager are internal components of TipserSDK and are only exposed to be used by tipser-widget (shouldn't be documented).
export const hashHandler = require('./hash-handler.ts').hashHandler();
export const dialogsManager = require('./dialogs-manager.ts').dialogsManager();

export type TipserSDKInstance = TipserSDKImpl;

export const TipserSDK = (posId: string, options: TipserSdkConfig): TipserSDKInstance => {
  return new TipserSDKImpl(posId, options);
};

class TipserSDKImpl {
  config: TipserSdkConfigCore;

  trackEventListeners = <any[]>[];
  thankYouWindowClosedListeners = <any[]>[];
  dialogClosedListeners = <any[]>[];
  cartItemsQuantityChangeListeners: Array<(cartSize: number) => void> = [];
  tipserApiClient: TipserApiClient;

  constructor(posId: string, options: TipserSdkConfig) {
    const config = initConfig(posId, options);
    const validationResult = validateConfig(config);
    if (!isConfigValid(config, validationResult)) {
      throw validationResult.error;
    }
    this.config = config;

    this.initPostMessageListener();
    hashHandler.init(this.config);
    dialogsManager.init(this.config);

    this.tipserApiClient = new TipserApiClient(this.config);
  }

  private initPostMessageListener() {
    window.addEventListener('message', (message: any) => {
      if (!message.data) {
        return;
      }

      const data = message.data;

      switch (message.data.command) {
        case 'tipser.widget.log':
          this.trackEventListeners.forEach((listener) => {
            listener(data.trackData);
          });
          break;
        case 'tipser.widget.thankYouPageClosed':
          const orderId = data.orderId;
          this.thankYouWindowClosedListeners.forEach((listener) => {
            listener(orderId);
          });
          break;
        case 'tipser.widget.close':
          this.tipserApiClient.invalidateCartSizeCache();

          this.dialogClosedListeners.forEach((listener) => {
            listener();
          });
          break;
        case 'tipser.widget.cartItemsQuantityChange':
          this.tipserApiClient.cartSizeCache.syncCartSizeFromApi(message.data.payload);
          this.cartItemsQuantityChangeListeners.forEach((listener) => listener(message.data.payload));
          break;
      }
    });
  }

  addTrackEventListener(listener: (event) => void) {
    this.trackEventListeners.push(listener);
  }

  removeTrackEventListener(listener: (event) => void) {
    const index = this.trackEventListeners.indexOf(listener);
    this.trackEventListeners.splice(index, 1);
  }

  addThankYouPageClosedListener(listener: (orderId) => void) {
    this.thankYouWindowClosedListeners.push(listener);
  }

  removeThankYouPageClosedListener(listener: (orderId) => void) {
    const index = this.thankYouWindowClosedListeners.indexOf(listener);
    this.thankYouWindowClosedListeners.splice(index, 1);
  }

  addDialogClosedListener(listener: () => void) {
    this.dialogClosedListeners.push(listener);
  }

  removeDialogClosedListener(listener: () => void) {
    const index = this.dialogClosedListeners.indexOf(listener);
    this.dialogClosedListeners.splice(index, 1);
  }

  addCartItemsQuantityChangeListener(listener: (cartSize: number) => void) {
    this.cartItemsQuantityChangeListeners.push(listener);
  }

  removeCartItemsQuantityChangeListener(listener: (cartSize: number) => void) {
    const index = this.cartItemsQuantityChangeListeners.indexOf(listener);
    this.cartItemsQuantityChangeListeners.splice(index, 1);
  }

  getCurrentCartSize(): Promise<number> {
    return this.tipserApiClient.getCurrentCartSize();
  }

  addToCart(productId: string, options: { posData?: string } = {}): Promise<void> {
    return this.tipserApiClient.canAccessCookies().then((canAccessCookies) => {
      if (canAccessCookies) {
        return this.tipserApiClient.addToCart(productId, options);
      }
      return this.openDirectToCheckoutDialog(productId);
    });
  }

  openProductDialog(productId: string) {
    dialogsManager.product(this.config.posId, productId, 'sdk', null);
  }

  openPurchaseDialog() {
    dialogsManager.purchase(this.config.posId);
  }

  openSearchDialog(searchTerm: string) {
    dialogsManager.search(this.config.posId, searchTerm);
  }

  openDirectToCheckoutDialog(productId: string): Promise<void> {
    return new Promise((resolve) => {
      let listenerHandler;
      const dialogClosedListener = () => {
        resolve();
        this.removeDialogClosedListener(listenerHandler);
      };
      listenerHandler = this.addDialogClosedListener(dialogClosedListener);
      dialogsManager.directToCheckout(this.config.posId, productId);
    });
  }

  openCollectionDialog(shopName: string, collectionName: string) {
    const collectionPath = shopName + '/' + collectionName;
    dialogsManager.directToCheckout(this.config.posId, collectionPath);
  }

  closeDialog() {
    dialogsManager.close();
  }

  analytics = {
    sendWhispererEvent: (payload: any) => this.tipserApiClient.sendWhispererEvent(payload),
    sendGaEvent: (payload: GaEventPayload) => this.tipserApiClient.sendGaEvent(payload),
  };
}

export default TipserSDK;
export { logger } from './logger';
