const queryString = require('query-string');

const parseQueryString = (qs?: string): object => {
  return queryString.parse(qs || location.search);
};

// https://gist.github.com/niyazpk/f8ac616f181f6042d1e0
function updateUrlQueryStringParameter(uri: string, key: string, value: string) {
  // remove the hash part before operating on the uri
  const i = uri.indexOf('#');
  const hash = i === -1 ? '' : uri.substr(i);
  uri = i === -1 ? uri : uri.substr(0, i);
  const re = new RegExp('([?&])' + key + '=.*?(&|$)', 'i');
  const separator = uri.indexOf('?') !== -1 ? '&' : '?';
  if (!value) {
    // remove key-value pair if value is empty
    uri = uri.replace(new RegExp('([?&]?)' + key + '=[^&]*', 'i'), '');
    if (uri.slice(-1) === '?') {
      uri = uri.slice(0, -1);
    }
    // replace first occurrence of & by ? if no ? is present
    if (uri.indexOf('?') === -1) uri = uri.replace(/&/, '?');
  } else if (uri.match(re)) {
    uri = uri.replace(re, '$1' + key + '=' + value + '$2');
  } else {
    uri = uri + separator + key + '=' + value;
  }
  return uri + hash;
}

const DialogsManager = require('./dialogs-manager.ts').dialogsManager;

const THANKYOU = 'Tipser_ThankYou';
const LIST = 'tl';
const CHECKOUT = 'Tipser_Checkout';
const QUERY_STRING_PARAM_NAME = 'tipser_checkout';

export const hashHandler = () => {
  const parseHash = () => {
    const fragment = window.location.hash.substring(1);
    const pairs = fragment.split('&');
    const result = {};
    for (let i = 0; i < pairs.length; i++) {
      const pair = pairs[i].split('-');
      const key = pair[0].toLowerCase();
      const value = pair[1];
      result[key] = value;
    }
    return result;
  };
  const buildHash = (parsedHash: object) => {
    if (parsedHash == undefined) {
      window.location.hash = '';
      return;
    }

    let result = '';
    for (const propertyName in parsedHash) {
      if (!parsedHash.hasOwnProperty(propertyName)) {
        continue;
      }
      if (result.length > 0) {
        result = result + '&';
      }
      if (parsedHash[propertyName]) {
        result = result + propertyName + '-' + parsedHash[propertyName];
      } else {
        result = result + propertyName;
      }
    }

    window.location.hash = result;
  };

  //TODO return as a module
  return {
    config: <any>null,
    dialogsManager: <any>null,

    THANKYOU: THANKYOU,
    LIST: LIST,
    CHECKOUT: CHECKOUT,
    QUERY_STRING_PARAM_NAME: QUERY_STRING_PARAM_NAME,

    shouldUseQueryString(key: string) {
      return this.config.redirectToQueryString && (key === THANKYOU || key === CHECKOUT);
    },

    getHashFromUrl(key: string): string {
      if (this.shouldUseQueryString(key)) {
        const parsedQueryString = parseQueryString();
        const hashString = parsedQueryString[QUERY_STRING_PARAM_NAME];
        if (hashString && hashString.indexOf(key) > -1) {
          return hashString.split('-')[1];
        } else {
          return null;
        }
      } else {
        const parsedHash = parseHash();
        return parsedHash[key.toLowerCase()];
      }
    },

    removeHashFromUrl(key: string) {
      if (this.shouldUseQueryString(key)) {
        const newUrl = updateUrlQueryStringParameter(window.location.href, QUERY_STRING_PARAM_NAME, '');
        if (history && history.pushState) {
          history.pushState(null, '', newUrl);
        } else {
          window.location.href = newUrl;
        }
      } else {
        const parsedHash = parseHash();
        delete parsedHash[key.toLowerCase()];
        buildHash(parsedHash);
      }
    },

    handleCheckoutHashes() {
      // handle confirmation modal
      const checkoutId = this.getHashFromUrl(THANKYOU);

      if (checkoutId) {
        this.dialogsManager.close();
        this.removeHashFromUrl(THANKYOU);
        this.dialogsManager.thanks(this.config.userid, checkoutId);
      }

      // Handle return to checkout
      const checkoutState = this.getHashFromUrl(CHECKOUT);

      if (checkoutState === 'resume') {
        this.dialogsManager.close();
        this.removeHashFromUrl(CHECKOUT);
        this.dialogsManager.purchase(this.config.userid, true);
      }
    },

    init(config: object) {
      this.config = config;
      this.dialogsManager = DialogsManager();
      this.dialogsManager.init(config);

      /**
       * Hash handler init
       * On checkout success/failure, there's a redirect to the host page performed
       * An information about the next step is encoded in the page url hash.
       * a) Checkout succeeded. Then, a THANKYOU hash is added and should be read by
       *    Tipser script in order to show a confirmation dialogue
       * b) Checkout failed (wrong card data or user pressed "cancel") - a CHECKOUT
       *    hash is added by PSP and script should open the checkout dialog with
       *    "resume=true" parameter, that is responsible for presenting the error
       *    to the user
       * Since usually Klarna/any other PSP does a "hard redirect", the host page
       * is reloaded with the hash added and it can be scanned in the init function (sync)
       * Sometimes, when paying with an invoice for example, some PSPs like Klarna,
       * does not do the full redirect, but sets the location using window.top.location=...
       * Which does not trigger full page reload. This is the reason why we need to poll
       * for the hash changes and act accordingly.
       */

      // Handle hashes on init
      this.handleCheckoutHashes();

      // POLL for changes in case the thank you/resume hash will appear AFTER the init was run
      setInterval(this.handleCheckoutHashes.bind(this), 500);
    },
  };
};
